.home {
  margin: 0;

  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;

    height: 655px;
    background-image: url(./images/Header.png);
    background-repeat: no-repeat;
  }
  
  &__header-blocks {
    display: flex;
    flex-direction: row;
  
    gap: 190px;
  }
  
  &__header-content {
    margin-left: 130px;
  
    display: flex;
    flex-direction: column;
  }
  
  &__header-title,
  &__header-title2,
  &__header-paragraph,
  &__header-button-link {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: normal;
  
    color: #fff;
  }
  
  &__header-title {
    margin: 24px 0 48px;
  
    font-size: 40px;
    line-height: 49px;
  }
  
  &__header-title2 {
    margin-bottom: 24px;
  
    width: 586px;
    height: 118px;
  
    font-size: 48px;
    line-height: 59px;
  }
  
  &__header-bold {
    font-weight: bold;
  }
  
  &__header-paragraph {
    margin-bottom: 32px;
  
    width: 580px;
    height: 56px;
  
    font-size: 20px;
    line-height: 142%;
  }
  
  &__header-button {
    display: flex;
    flex-direction: column;
  
    width: 154px;
    height: 56px;
    border-radius: 8px;
  
    background-color: #fff;
  }
  
  &__header-button-link {
    padding: 17px 31px;
  
    font-size: 16px;
    line-height: 142%;
  
    color: #3a80ba;
    text-decoration: none;
  }
  
  &__header-link {
    margin-top: 72px;
  }
  
  &__header-img {
    background-image: url(./images/mobile.png);
    background-position: center top;
    background-repeat: no-repeat;
  
    width: 321px;
    height: 561px;
  }

  @media (min-width: 1441px) {
    &__header {
      background-image: none;
      background-color: #3a80ba;
    }
  }

  @media (max-width: 1235px) {
    &__header-blocks {
      gap: 0;
    }

    &__header-content {
      margin: 0;
    }

    &__header-img {
      display: none;
    }
  }

  @media (max-width: 800px) {
    &__header-content {
      gap: 15px;
    }

    &__header-title2 {
      width: 420px;
      height: 56px;
  
      font-size: 20px;
      line-height: 142%;
    }

    &__header-paragraph {
      width: 420px;
    }
  }

  @media (max-width: 581px) {
    &__header-title2 {
      width: 320px;
    }

    &__header-paragraph {
      width: 320px;
      height: max-content;
    }
  }

  @media (max-width: 384px) {
    &__header-title2,
    &__header-paragraph {
      width: 220px;
      height: min-content;
    }
  }
}
